* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Mulish", sans-serif;
  color: #2f2e41;
}
body {
  overflow-x: hidden;
}
ul.navbar-nav.ml-auto.mb-2.mb-lg-0 {
  margin-left: auto;
}
a.navbar-brand {
  font-size: 20px;
  color: #6c63ff !important;
}
.nav-link,
.navbar-expand-lg .navbar-nav .nav-link {
  padding: 0;
  margin: 0 10px;
  color: #2f2e41 !important;
}
.menu_active {
  font-weight: 600;
  border-bottom: 2px solid #6c63ff;
}

.navbar_custom .container-fluid,
.navbar_custom .col-md-10 {
  padding: 0;
}

.TextHelight {
  color: #6c63ff;
}

/* ================= Home Page */

section.common_banner {
  min-height: 80vh;
  padding-top: 10%;
}
section.contactUs {
  min-height: 80vh;
  padding-top: 4%;
}
.btn_common {
  color: #6c63ff;
  text-decoration: none;
  letter-spacing: 1px;
  font-size: 18px;
  border: 1px solid;
  border-radius: 50px;
  padding: 8px 30px;
  transition: all 0.3s ease-in-out;
  display: inline-block;
  margin-top: 20px;
}
.btn_common:hover {
  color: #fff;
  background: #6c63ff;
}
.img-animated {
  animation: animatated 2s ease-in-out infinite alternate-reverse both;
}
@keyframes animatated {
  0% {
    transform: translateY(30px);
  }
  100% {
    transform: translateY(-30px);
  }
}

.our_service {
  padding-bottom: 100px;
}

.our_service .card > img {
  max-height: 200px;
  padding: 20px;
}
.our_service .card-body .btn {
  background: #6c63ff;
  border: 1px solid;
}
h5.card-title {
  font-weight: 600;
}

.navbar_custom .navbar-expand-lg {
  padding-top: 30px;
}

section.contactUs .cotainer-fluid {
  max-width: 1133px;
  margin: 0 auto 50px;
}
section.contactUs input,
section.contactUs textarea {
  width: 100%;
  margin: 10px 0;
  padding: 10px 0;
  border: none;
  border-bottom: 1px solid #ccc;
}
textarea {
  height: 100px;
}
section.contactUs button {
  min-width: 150px;
}

input:focus,
textarea:focus {
  outline: 1px solid #6c63ff;
}
input:focus-visible,
textarea:focus-visible {
  outline: 1px solid #6c63ff;
}
.error-heading {
  font-size: 70px;
  text-align: center;
  margin-top: 4%;
  color: #6c63ff;
}
.error_img {
  max-width: 400px;
  margin: 0 auto;
  text-align: center;
}
.btn-primary {
  background-color: #6c63ff;
  border-color: #6c63ff;
}
.btn-secondary {
  background-color: #2f2e41;
  border-color: #2f2e41;
}

@media screen and (max-width: 990px) {
  div#navbarNavDropdown {
    background: #6c63ff;
    padding: 30px;
    text-align: center;
    margin-top: 20px;
  }
  .nav-link,
  .navbar-expand-lg .navbar-nav .nav-link {
    padding: 0;
    margin: 0;
    font-size: 20px;
    color: #fff !important;
    text-transform: uppercase;
    display: inline-block;
    padding-bottom: 5px;
    margin-bottom: 16px;
  }
  .menu_active {
    font-weight: 600;
    border-bottom: 2px solid #fff;
  }
  section.common_banner .row .col-md-6:first-child,
  section.contactUs .row .col-md-6:first-child {
    order: 2;
    margin-top: 60px;
  }
  section.common_banner .row .col-md-6:last-child,
  section.contactUs .row .col-md-6:last-child {
    margin-top: 60px;
  }
  section.common_banner .cotainer-fluid,
  section.contactUs .cotainer-fluid {
    padding: 0 20px;
  }
  .h1,
  h1 {
    font-size: 25px;
  }
  section.common_banner .row .col-md-6:first-child h5.mt-3.mb-2 {
    font-size: 16px;
  }
  .btn_common {
    margin-bottom: 50px;
  }
  section.contactUs h1.mt-5.mb-5.text-center {
    margin: 0 !important;
    margin-bottom: 20px !important;
  }
  .error-heading {
    font-size: 30px;
  }
  .error_img {
    max-width: 200px;
  }
  section.contactUs .row .col-md-6 {
    order: 2;
  }
  section.contactUs .row .col-md-5 {
    margin: 50px 0;
  }
}
